import { Button, Image } from "react-bootstrap"
import { baseUrl } from "../AssetsHelper"
const EmptyWithIcon = ({title, desc, button, icon, isFullScreen}) => {
       const size = isFullScreen ? 86 : 66
       return <div className="mx-auto f-center w-100">
                     <div className="py-5 text-center w-100 justify-content-center">
                            <div className="mx-auto px-4" style={{maxWidth:520}}>
                                   {icon ? <Image data-mode="light" className="mb-4" src={baseUrl(`svg/icons/${icon}.svg`)} height={size} width={size} alt={icon} fluid/> : null}
                                   {title ? <h2 className="text-center h3">{title}</h2> : null}
                                   {desc ? <p className="fs-8 text-muted fw-medium">{desc}</p> : null}
                                   {button ? <Button onClick={() => button.action()} size={isFullScreen?'lg':'md'} className="my-3">{button.text}</Button> : null}
                            </div>
                     </div>
              </div>
}

export default EmptyWithIcon